import { createRouter, createWebHashHistory } from 'vue-router'
import networkList from '@/api/abis/network.js'

const routes = [
  {
    path: '/',
    redirect: `/swap/${networkList[0].Dex[0].symbol}`
  },{
    path: '/swap/:key',
    name: 'swap',
    component: () => import('@/views/swap/index.vue'),
    meta:{
      title:"Tea Dao-Swap"
    }
  },{
    path: '/stake',
    name: 'stake',
    component: () => import('@/views/pledge/index.vue'),
    meta:{
      title:"Tea Dao-Stock"
    }
  },{
    path: '/dor',
    name: 'dor',
    component: () => import('@/views/dor/index.vue'),
    meta:{
      title:"Tea Dao-Dor"
    }
  },{
    path: '/airdrop',
    name: 'airdrop',
    component: () => import('@/views/airdrop/index.vue'),
    meta:{
      title:"Tea Dao-Airdrop"
    }
  },
  {
    path: '/tools',
    name: 'tools',
    component: () => import('@/views/tools/index.vue'),
    meta:{
      title:"Tea Dao-Tools"
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
