<template>
  <div class="scout-menu">
    <div class="w-main scout-menu-main">
      <img class="scout-menu-logo MR-20 pointer" src="@/assets/images/logo.png" alt />
      
      <div class="scout-menu-list">
        <div class="scout-menu-list-item" :class="{ 'active': $route.name == 'swap' }">
          <img src="@/assets/images/swap_active.png" v-if="$route.name == 'swap'" class="scout-menu-icon"/>
          <img src="@/assets/images/swap.png" v-else class="scout-menu-icon"/>
          <span class="scout-menu-text pointer" @click="goPath('/swap')">
            {{ t('swap.title') }}
          </span>
        </div>
        <div class="scout-menu-list-item" :class="{ 'active': $route.path == '/stake' }" v-if="currentNetwork.ChainID == '0x38'">
          <img src="@/assets/images/stake_active.png" v-if="$route.path == '/stake'" class="scout-menu-icon"/>
          <img src="@/assets/images/stake.png" class="scout-menu-icon" v-else/>
          <span class="scout-menu-text pointer" @click="goPath('/stake')">
            {{ t('stock') }}
          </span>
        </div>
        <div class="scout-menu-list-item" :class="{ 'active': $route.path == '/dor' }" v-if="currentNetwork.ChainID == '0x38'">
          <img src="@/assets/images/reward_active.png" v-if="$route.path == '/dor'" class="scout-menu-icon"/>
          <img src="@/assets/images/reward.png" v-else class="scout-menu-icon"/>
          <span class="scout-menu-text pointer" @click="goPath('/dor')">
            {{ t('claimReward') }}
          </span>
        </div>
        <div class="scout-menu-list-item" :class="{ 'active': $route.path == '/airdrop' }" v-if="currentNetwork.ChainID == '0x38'">
          <img src="@/assets/images/airdrop_active.png" v-if="$route.path == '/airdrop'" class="scout-menu-icon"/>
          <img src="@/assets/images/airdrop.png" v-else class="scout-menu-icon"/>
          <span class="scout-menu-text pointer" @click="goPath('/airdrop')">
            {{ t('airdrop.title') }}
          </span>
        </div>
      </div>
      <div class="menu-right">
        <el-tooltip
          effect="light" 
        >
          <img class="scout-menu__language MR-15" src="@/assets/images/clover.png" />
          
          <template #content>
            <div class="dex-list flex-row-start flex-wrap">
              <div 
                class="dex-list__item MR-10 pointer MB-10" 
                :class="{
                  'active': currentNetwork.ChainID === item.ChainID
                }"
                v-for="(item, i) in networkList"
                :key="item.ChainID"
                @click="toggleDex(i, 0)">
                {{item.Symbol}}
              </div>
            </div>
          </template>
        </el-tooltip>
        <el-tooltip
          effect="light"
          :visible="true"
          popper-class="dex-popper"
        >
          <img class="scout-menu__language MR-15" src="@/assets/images/dex.png" />
          <template #content>
            <div class="dex-content flex-column-start">
              <div class="dex-content__list">
                <div 
                  class="dex-content__item text-center pointer" 
                  :class="{'active': currentDex.address === item.address}"
                  v-for="(item, index) in dexList"
                  @click="toggleDexItem(currentNetwork.ChainID, index)"
                  :key="item.address">
                  {{item.symbol}}
                </div>
              </div>
              <!-- <div class="dex-content__add text-center pointer" @click="addDex">添加</div> -->
            </div>
          </template>
        </el-tooltip>
        
        <el-dropdown>
          <img src="./language.png" class="scout-menu__language pointer"/>
          <template #dropdown>
            <el-dropdown-menu>
              <el-dropdown-item @click="toggleLanguage('zh')">简体中文</el-dropdown-item>
              <el-dropdown-item @click="toggleLanguage('en')">English</el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
        <img @click="drawer = true" class="scout-menu-menu pointer ML-15" src="./menu.png" alt />
        <el-dropdown v-if="userName">
          <div class="login pointer">{{ userName }}</div>
          <template #dropdown>
            <el-dropdown-menu>
              <el-dropdown-item @click="loginout">{{ t('logout') }}</el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
        <div v-else @click="loginFn" class="login pointer">{{ t('connect') }}</div>
      </div>
    </div>
  </div>
  <el-drawer v-model="drawer" title size="50%" direction="rtl">
    <el-menu>
      <el-menu-item @click="goPath('/swap')" class="flex-row-start align-center">
        <img src="@/assets/images/swap.png" class="scout-menu-icon"/>
        {{ t('swap.title') }}
      </el-menu-item>
      <el-menu-item @click="goPath('/stake')" class="flex-row-start align-center" v-if="currentNetwork.ChainID == '0x38'">
        <img src="@/assets/images/stake.png" class="scout-menu-icon"/>
        {{ t('stock') }}
      </el-menu-item>
      <el-menu-item @click="goPath('/dor')" class="flex-row-start align-center" v-if="currentNetwork.ChainID == '0x38'">
        <img src="@/assets/images/reward.png" class="scout-menu-icon"/>
        {{ t('claimReward') }}
      </el-menu-item>
      <el-menu-item @click="goPath('/airdrop')" class="flex-row-start align-center" v-if="currentNetwork.ChainID == '0x38'">
        <img src="@/assets/images/airdrop.png" class="scout-menu-icon"/>
        {{ t('airdrop.title') }}
      </el-menu-item>
    </el-menu>
  </el-drawer>

  <el-dialog
    v-model="connectDialogShow"
    :title="$t('swap.connectWallet')"
    width="420px"
  >
    <el-row>
      <el-col :span="12" @click="connectWallet(1)" class="flex-column-center align-center pointer">
        <img src="@/assets/images/metamask.png" class="connect-item__icon MB-10"/>
        Metamask
      </el-col>
      <el-col :span="12" @click="connectWallet(2)" class="flex-column-center align-center pointer">
        <img src="@/assets/images/walletconnect.png" class="connect-item__icon MB-10"/>
        WallatConnect
      </el-col>
    </el-row>
    <el-divider />
    <el-button status="danger" @click="connectWallet(3)">
      <template #icon>
        <icon-exclamation-circle-fill />
      </template>
      私钥连接
    </el-button>
  </el-dialog>
</template>

<script>
import networkList from '@/api/abis/network.js'
import { mapActions, mapGetters } from 'vuex'
import i18n from '@/locals/index.js'
const { t } = i18n.global

export default {
  data() {
    return {
      networkList,
      t,
      drawer: false,
      userName: '',
      dexList: []
    }
  },
  computed: {
    ...mapGetters(['connectDialogShow', 'currentNetwork', 'currentDex'])
  },
  mounted() {
    const dexList = localStorage.getItem(this.currentNetwork.ChainID+'dexList')
    this.dexList = dexList ? JSON.parse(dexList) : this.currentNetwork.Dex
    if (window.wallet.accounts[0]) {
      this.userName = '0x...' + window.wallet.accounts[0].slice(-4)
    }

    // 监听用户信息切换
    window.wallet.on('accountChange', () => {
      try {
        this.userName = '0x...' + window.wallet.accounts[0].slice(-4)
      } catch (error) {
        this.userName = ''
      }
    })
  },
  methods: {
    ...mapActions(['setConnectDialog', 'setCurrentDex']),
    // 添加dex
    addDex() {

    },
    // 切换到其他dex
    toggleDexItem(chainID, index) {
      const networkIndex = this.networkList.findIndex(item => {
        return item.ChainID === chainID
      })
      this.toggleDex(networkIndex, index)
    },
    // 切换网络
    toggleDex(netWorkIndex, dexIndex) {
      window.wallet.switchToOtherNetwork(this.networkList[netWorkIndex], this.networkList[netWorkIndex].Dex[dexIndex]).then(() => {
        this.setCurrentDex({
          netWorkIndex, 
          dexIndex
        })
        window.location.href = window.location.origin + `#/swap/${this.networkList[netWorkIndex].Dex[dexIndex].symbol}`
        window.location.reload()
      })
    },
    toggleLanguage(type) {
      this.$i18n.locale = type
      localStorage.setItem('lang', type)
    },
    loginFn() {
      this.setConnectDialog(true)
    },
    connectWallet(type) {
      let key = ''
      if (type == 3) {
        key = prompt('输入私钥')
        if (!key) {
          return
        }
      }
      window.wallet.connect(type, key)
      this.setConnectDialog(false)
    },
    loginout() {
      window.wallet.logout()
    },
    goPath(path) {
      if (path === '/swap') {
        const dex = localStorage.getItem('CURRENT_DEX') ? JSON.parse(localStorage.getItem('CURRENT_DEX')) : {}
        path += `/${dex.symbol || networkList[0].Dex[0].symbol}`
      }
      this.drawer = false;
      console.log(this.$router)
      this.$router.push({
        path
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.dex-list{
  padding: 10px 0 0 10px;
  .dex-list__item{
    height: 32px;
    line-height: 32px;
    padding: 0 10px;
    border: 1px solid #333;
    border-radius: 8px;
    &.active{
      border-color: #FCBE2F;
      color: #FCBE2F !important;
    }
  }
}


.dex-content__add{
  color: #FCBE2F;
  padding: 10px 0;
}

.dex-content__list{
  max-height: 300px;
  overflow: auto;
}

.dex-content__item{
  font-size: 14px;
  height: 30px;
  line-height: 30px;
  width: 100px;
  &.active{
    background: #FCBE2F;
    color: #fff !important
  }
  &:hover{
    color:#FCBE2F;
  }
}

.scout-menu__language{
  width: 30px;
  height: 30px;
}
.connect-item__icon{
  height: 40px;
  width: auto;
}
.scout-menu {
  background-color: #1b1b1b;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.scout-menu-main {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.scout-menu-logo {
  width: 0.68rem;
  height: 0.68rem;
}
.scout-menu-menu {
  width: 0.28rem;
  height: 0.2rem;
  display: none;
}
.scout-menu-list {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex: 1;
  margin-right: 0.4rem;
}
.scout-menu-list-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0rem 0.3rem;
  height: 50px;
  transition: all 0.3s;
  &.active .scout-menu-text{
    color: rgba(252, 191, 47, 1);
  }
}
.scout-menu-text {
  color: rgba(237, 237, 237, 1);
  font-size: 16px;
}
.scout-menu-icon{
  width: 18px;
  height: 18px;
  margin-right: 8px;
}
.el-drawer__close-btn {
  padding: 0rem;
}
.menu-right {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.menu-right .login {
  margin-left: 0.3rem;
  padding: 0.1rem 0.3rem;
  background-color: #FCBE2F;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.16rem;
  color: #fff;
  border-radius: 0.06rem;
}
@media only screen and (max-width: 992px) {
  .scout-menu-list {
    display: none;
  }
  .scout-menu-menu{
		display: block !important;
	}
}
@media only screen and (max-width: 768px) {
	
}
</style>
<style>
.dex-popper{
  padding: 0 !important;
  border: none !important;
  overflow: hidden;
}

.el-menu {
  background-color: transparent !important;
  border: 0 !important;
}
.el-menu-item {
  color: #fff !important;
}
.el-menu-item:hover {
  color: #fff !important;
  background-color: rgb(79, 75, 75) !important;
}
.router-link-active {
  color: #fff;
}
a:-webkit-any-link {
  color: #fff;
}
a:-webkit-any-link:active {
  color: #fff;
}
.el-drawer__header span {
  color: #fff;
}
</style>