import { createStore } from 'vuex'
import networkList from '@/api/abis/network.js'

const currentNetwork = localStorage.getItem('CURRENT_NETWORD') ? JSON.parse(localStorage.getItem('CURRENT_NETWORD')) : networkList[0]

export default createStore({
  state: {
    // 登录弹框是否显示
    connectDialogShow: false,
    // 网络列表
    networkList,
    // 当前网络
    currentNetwork,
    // 当前交易所
    currentDex: localStorage.getItem('CURRENT_DEX') ? JSON.parse(localStorage.getItem('CURRENT_DEX')) : currentNetwork.Dex[0],
    // 当前rpc
    currentRpc: localStorage.getItem('CURRENT_RPC') || currentNetwork.Rpcs[0]
  },
  mutations: {
    // 设置当前交易所
    SET_CURRENT_DEX: (state, {netWorkIndex, dexIndex}) => {
      state.currentNetwork = networkList[netWorkIndex]
      // 因为可以自定义添加dex，所以需要先从缓存找
      let dexList = localStorage.getItem(state.currentNetwork.ChainID+'dexList')
      dexList = dexList ? JSON.parse(dexList) : networkList[netWorkIndex].Dex
      state.currentDex = dexList[dexIndex]
      localStorage.setItem('CURRENT_NETWORD', JSON.stringify(state.currentNetwork))
      localStorage.setItem('CURRENT_DEX', JSON.stringify(state.currentDex))
    },
    // 设置钱包弹框是否显示
    SET_CONNECT_DIALOG(state, bool) {
      state.connectDialogShow = bool
    },
    SET_CURRENT_RPC: (state, rpc) => {
      state.currentRpc = rpc
      localStorage.setItem('CURRENT_RPC', rpc)
    }
  },
  actions: {
    // 设置当前交易所
    setCurrentDex({ commit }, {netWorkIndex, dexIndex}) {
      commit('SET_CURRENT_DEX', {netWorkIndex, dexIndex})
    },
    // 设置当前rpc
    setCurrentRpc({ commit }, rpc) {
      commit('SET_CURRENT_RPC', rpc)
    },
    // 设置钱包弹框是否显示
    setConnectDialog({ commit }, bool){
      commit('SET_CONNECT_DIALOG', bool)
    }
  },
  getters: {
    // 获取网络列表
    networkList(state) {
      return state.networkList
    },
    // 获取当前网络
    currentNetwork(state) {
      return state.currentNetwork
    },
    // 获取当前交易所
    currentDex(state) {
      return state.currentDex
    },
    currentRpc(state) {
      return state.currentRpc
    },
    // 钱包链接弹框是否显示
    connectDialogShow(state) {
      return state.connectDialogShow
    }
  },
  modules: {

  }
})
